@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap');

.about-top {
    margin-top: 15vh;
    margin-left: 15px;
}

.about {
    background-color: #073642;
    min-height: 88vh;
    overflow: hidden;
}

.about-title {
    font-family: "DM Serif Display";
    color: #839496;
    width: 500px;
    font-size: 3rem;
    border-bottom: 1px solid #839496; 
    line-height: 0.1em;
}

.about-title-text { 
    background: #073642; 
    padding: 0 10px; 
}

.text {
    width: 60vw;
}

.about-me-text {
    margin-top: 50px;
}

.about-me-paragraph {
    font-family: "Noto Sans JP";
    font-size: 1.2rem;
    color: #93a1a1;
    margin-left: 25px;
    margin-right: 20px;
}


.photo {
    /*filter: blur(20px);*/
    opacity: 0.7;
    margin-top: 10vh;
    width: 80vw;
    border-color: #64ffda !important;
    margin-left: -10px;
    transition-duration: 0.8sec;
}

.photo:hover {
    opacity: 1;
    box-shadow: 0 0 3px 2px #64ffda;
}

.skills {
    color: #93a1a1;
    margin-left: 15px;
}

.skill-icon {
    color: #64ffda;
    margin-right: 5px;
}


@media (max-width: 750px) {

    .photo {
        margin-left: 30px;
    }
}